.not-found {
	@include centre(both);
	user-select: none;
	text-align: center;
	&__nums {
		font-family: "geo-heavy";
		font-size: 270px;
		color: #d3d8dc;
		@include max-screen($small-tablet) {
			font-size: 160px; }
		@include max-screen($smartphone-landscape) {
			font-size: 120px; } }


	&__text {
		font-family: "geo-light";
		font-size: 30px;
		line-height: 48px;
		text-transform: uppercase;
		@include max-screen($small-tablet) {
			font-size: 24px; }
		@include max-screen($smartphone-landscape) {
			font-size: 22px;
			line-height: 30px; }
		@include max-screen($smartphone-portrait) {
			font-size: 14px; } } }
