$cube-breakpoint: 700px;

.p-cube {
	//margin-top: 100px
	//perspective: 100px
	//perspective: 1000px
	perspective: 6000px;
 }	//perspective-origin: 50% 50%

.cube {
	position: relative;
	//margin: 200px auto
	margin: 100px auto;
	//width: 630px
	//height: 362px
	width: 790px;
	height: 430px;
	transform-style: preserve-3d;
	//transform-origin: 50% 50% -181px
	//transform: rotateX(-20deg) rotateY(32deg)
	transform: rotateX(-30deg) rotateY(32deg);

	&--empty {
		.cube__side {
			background: none!important;
			border: 5px solid $color-black; } }


	@include max-screen($cube-breakpoint) {
		width: 300px;
		height: 150px;
 }		//transform-origin: 50% 50% -75px

	&__side {
		opacity: 0;
		position: absolute;
		//border: 5px solid #000000
		color: $color-white;
		font-size: 40px;
		line-height: 362px;
 }		//transition: all 1s


	.front {
		background-image: url("../img/cube-4.png");
		@include bg-cover;
		width: 790px;
		height: 430px;
		transform-origin: bottom center;
		transform: rotateX(-90deg);
		@include max-screen($cube-breakpoint) {
			width: 300px;
			height: 150px; } }

	.left {
		width: 430px;
		height: 430px;
		transform-origin: left bottom;
		background: #d4d9dd;
		//transform: rotateY(90deg)
		//transform: rotate3d(90, 45, 0, 90deg)
		@include max-screen($cube-breakpoint) {
			width: 150px;
			height: 150px; } }

	.right {
		width: 430px;
		height: 430px;
		right: 0;
		transform-origin: right bottom;
		background: #d4d9dd;
		//transform: rotateY(-90deg) rotateX(90deg)
		@include max-screen($cube-breakpoint) {
			width: 150px;
			height: 150px; } }

	.back {
		background-image: url("../img/cube-2.png");
		@include bg-cover;
		width: 790px;
		height: 430px;
		transform-origin: bottom center;
		//transform: translateZ(-362px) rotateY(180deg) rotateX(-90deg)
		@include max-screen($cube-breakpoint) {
			width: 300px;
			height: 150px; } }

	.bottom {
		background-image: url("../img/cube-3.png");
		@include bg-cover;
		width: 790px;
		height: 430px;
		transform-origin: bottom center;
		transform: rotateX(90deg);
		@include max-screen($cube-breakpoint) {
			width: 300px;
			height: 150px; } }

	.top {
		background-image: url("../img/cube-1.png");
		@include bg-cover;
		//opacity: 1
		width: 790px;
		height: 430px;
		transform-origin: top center;
		//transform: translateZ(-362px) rotateX(-110deg)
		@include max-screen($cube-breakpoint) {
			width: 300px;
			height: 150px; } } }

.animate-cube {
	.front,
	.left,
	.right,
	.back {
		transform: rotateX(0deg); } }

